#keep main .container {
  height: 660px;
}
.navigation-drawer__border {
  display: none;
}
.text {
  font-weight: 400;
}
.footer {
  max-height: 2px !important;
}

/* @group Blink */
.blink {
  -webkit-animation: blink .75s linear infinite;
  animation: blink .75s linear infinite;
}
@-webkit-keyframes blink {
0% {
    opacity: 1;
}
50% {
    opacity: 1;
}
50.01% {
    opacity: 0;
}
100% {
    opacity: 0;
}
}
@keyframes blink {
0% {
    opacity: 1;
}
50% {
    opacity: 1;
}
50.01% {
    opacity: 0;
}
100% {
    opacity: 0;
}
}

/* @end */













